import { Button,Stack,Typography,useTheme,Divider,AppBar } from "@mui/material"
import { Link, Outlet, useNavigate } from "react-router-dom";
import { Icon } from "@iconify/react";

export function NormalLayout() {
    return (
        <div style={{ minHeight:"100vh",height:'100%' }}>
            <Header/>
            <Outlet/>
        </div>
    )
}

export function FullScreenLayout() {
    return(
        <div style={{ minHeight:"100vh"}}>
            <Outlet/>
        </div>
    )
}

const Header = () => {
    const theme = useTheme();
    const navigate = useNavigate();
    return (
        <AppBar
        position="sticky"
        color="transparent"
        sx={{
            backdropFilter: 'blur(10px)',
            // backdropFilter: 'invert(90%) blur(5px)',
            boxShadow: "none",
        }}
    >
        <Stack style={{flexDirection:"row",display:'flex',alignItems:'center',padding:'1em 2em', position:'sticky'}}>
                <Link to={"/*"}>
                    <Button>
                        <Typography variant="title">Pandora project</Typography>
                    </Button>
                </Link>
                <Divider orientation="vertical" variant="middle" flexItem sx={{borderColor:'rgba(255, 255, 255, 0.25)',padding:'0 1em'}}/>
                <Button onClick={() => navigate("/home")} sx={{marginLeft:'1em'}}>
                    <Typography variant="title">Game</Typography>
                </Button>
                <Button onClick={() => navigate("/info")} sx={{marginLeft:'1em',justifySelf:'end'}}>
                    <Icon icon="icon-park-outline:info" color={theme.palette.goldOverride.main}/>
                </Button>
        </Stack>
    </AppBar>
    )
}