import React from "react";
import { 
    Card,
    CardActionArea,
    CardMedia,
    CardContent,
    Typography
    } from "@mui/material";

interface CardLolProps {
    bottom?:JSX.Element;
    useDisableRipple?:boolean;
    varianteImg?:'contain' | 'cover'; /**@default 'cover'**/
    content:{
        img:string;
        imgPosition?:string;
        title:string;
        link?:string;
    }
}

export default function CardFillImg({useDisableRipple,bottom,content,varianteImg}:CardLolProps) {
    
    const ImagePosition = (image_position?:string) => {
        switch (image_position) {
          case "center":
            return 50
          case "right":
            return 100
          case "middle-right":
            return 75
          case "middle-left":
            return 25
          case "left":
            return 0
          default: return 50
        }
    }

    const [isClicked, setIsClicked] = React.useState(false);

    const handleCardClick = () => {
        setIsClicked(!isClicked); // Inverse l'tat de clic de la carte
        window.open(`${content.link}`, '_blank');
    };

    return (
      <Card
        sx={{
          height: '100%',
          width: '100%',
          // padding: '0.5rem',
          background:'white',
          borderRadius:'1em',
        }}
      >
        <CardActionArea
          disableRipple={useDisableRipple}
          sx={{ position: 'relative', height: '100%', width: '100%' }}
          onClick={handleCardClick}
        >
          <CardMedia
            component="img"
            image={content?.img}
            alt={content?.title}
            sx={{
              objectFit: `${varianteImg}`, // Make sure the image is fully visible
              padding:varianteImg == 'contain'? '0.5em' : '0',
              height: '100%', // rajouter un padding uniquement si on est en containe
              width: '100%',
              objectPosition: `${ImagePosition(content?.imgPosition)}% 50%`
            }}
          />
          <CardContent
            sx={{
              position: 'absolute',
              bottom: 0,
              width: '100%',
              padding: '0',
              margin: '0',
              backdropFilter: 'blur(5px)',
              backgroundColor: 'rgba(0, 0, 0, 0.5)'
            }}
          >
            <Typography
              variant={"title"}
              gutterBottom
              component="div"
              fontSize={{ xs: '1.2rem',sm:'1.5rem'}}
              textAlign={'center'}
              style={{textShadow: `0px 0px 7px black`}}
            >
              {content?.title}
            </Typography>
            {bottom}
          </CardContent>
        </CardActionArea>
      </Card>
    )
}