import { createTheme } from "@mui/material/styles";

import InterItalic from "./fonts/Inter-Italic.woff2";
import InterRegular from "./fonts/Inter-Regular.woff2";
import SofiaProBold from "./fonts/SofiaProBold.woff2";
import SofiaProBoldItalic from "./fonts/SofiaProBold-Italic.woff2";

const fonts = [
    {
        "@font-face": {
            fontFamily: '"Inter Italic"',
            fontDisplay: "swap",
            fontStyle: "italic",
            fontWeight: "normal",
            src: `url(${InterItalic}) format('woff2')`,
        },
    },
    {
        "@font-face": {
            fontFamily: '"Inter Regular"',
            fontDisplay: "swap",
            fontStyle: "normal",
            fontWeight: "normal",
            src: `url(${InterRegular}) format('woff2')`,
        },
    },
    {
        "@font-face": {
            fontFamily: '"SofiaProBold"',
            fontDisplay: "swap",
            fontStyle: "normal",
            fontWeight: "bold",
            src: `url(${SofiaProBold}) format('woff2')`,
        },
    },
    {
        "@font-face": {
            fontFamily: '"SofiaProBold Italic"',
            fontDisplay: "swap",
            fontStyle: "italic",
            fontWeight: "bold",
            src: `url(${SofiaProBoldItalic}) format('woff2')`,
        },
    },
];

export const baseTheme = createTheme({
    components: {
        MuiCssBaseline: {
            styleOverrides: {
                fallbacks: fonts,
            },
        },
    },
});
