import React from "react";
import { Character } from "../Page/WhoPage";
import { 
    Card,
    CardActionArea,
    CardMedia,
    CardContent,
    Typography
    } from "@mui/material";

interface CardLolProps {
    character:Character | undefined;
    bottom?:JSX.Element;
    useDisableRipple?:boolean;
    useEffectShadow?:boolean;
}

export default function CardLol({character,useDisableRipple,bottom,useEffectShadow}:CardLolProps) {
    
    const ImagePosition = (image_position?:string) => {
        switch (image_position) {
          case "center":
            return 50
          case "right":
            return 100
          case "middle-right":
            return 75
          case "middle-left":
            return 25
          case "left":
            return 0
          default: return 50
        }
    }

    const [isClicked, setIsClicked] = React.useState(false);

    const handleCardClick = () => {
        setIsClicked(!isClicked); // Inverse l'tat de clic de la carte
    };

    return (
        <Card sx={{overflow:'visible'}}>
            <CardActionArea disableRipple={useDisableRipple} sx={{ position: 'relative',height:{xs:'10em',sm:'14em',lg:'15em',xl:'18em'}}} onClick={handleCardClick}>
              <CardMedia
                component="img"
                height="100%"
                image={character?.image_url}
                alt={character?.name}
                sx={{ objectFit: 'cover',objectPosition:`${ImagePosition(character?.image_position)}% 50%`}} // Assure que l'image couvre la totalit de la zone
                />
              <CardContent sx={{position: 'absolute', bottom: 0, width: '100%', color: 'white', padding: '0.5em',margin:'0px' }}>
                <Typography variant={"title"} gutterBottom component="div" sx={{textShadow: `0px 0px 7px black`,fontSize:{xs:'1em',lg:'1.8em',xl:'2em'}}}>
                  {character?.name}
                </Typography>
                {bottom}
              </CardContent>
              {useEffectShadow && (
                <div style={{  
                position: 'absolute',top: 0,left: 0,width: '100%',height: '100%',
                boxShadow: isClicked? `inset 0px 0px 10em black` : `0px 0em 0.5em black`,
                background: isClicked? 'rgba( 0, 0, 0, 0.8)' : '',
                pointerEvents: 'none' // Permet aux vnements de passer  travers la div
              }}/>
            )}
            </CardActionArea>
        </Card>
    )
}