import React from "react";
import { 
    Grid,
    Typography,
    Stack,
    CardActions,
    Box,
    Button,
    Menu,
    MenuItem,
    useTheme,
    Paper,
    styled
    } from "@mui/material";
import CardLol from "../Components/CardLol";
import { SwitchWithTitle } from "../Components/Switch";
import Data from './../../../../Data/DataLeagueOfLegends.json'
import { Icon } from "@iconify/react";
import { Filter } from "../Filter/Filter";

export interface Character {
    name: string;
    description: string;
    image_url: string;
    image_position: string;
    lord: {
      lane: string;
      role: string;
      sex: string;
      region: string;
    };
}

export interface Region {
    name: string;
    region_icon: string;
    region_img: string;
}

export interface Role {
    name: string;
    role_icon: string;
}

export interface Lane {
    name: string;
}

export interface RegionOverrideProps extends Region {
  isSelected: boolean;
}

export interface RoleOverrideProps extends Role {
  isSelected: boolean;
}

export interface LaneOverrideProps extends Lane {
  isSelected: boolean;
}

export const WhoPage = () => {

  // const lanesOverride:LaneOverrideProps[] = Data.lanes.map((lane: Lane) => {
  //     return { ...lane, isSelected: false };
  // });

  // const regionsOverride:RegionOverrideProps[] = Data.regions.map((region: Region) => {
  //     return { ...region, isSelected: false };
  // });

  // const rolesOverride:RoleOverrideProps[] = Data.roles.map((role: Role) => {
  //     return { ...role, isSelected: false };
  // });

  const [selectedCharacter, setSelectedCharacter] = React.useState<Character | undefined>();
  return (
      <Grid container columns={12} sx={{flex:1,display:'flex'}}>
          {/* <Filter roles={rolesOverride} regions={regionsOverride} lanes={lanesOverride}/> */}
          <Grid item xs={12} sm={4} md={3} lg={2}>
              <CharacterDashboard selectedCharacter={selectedCharacter} setSelectedCharacter={setSelectedCharacter}/>
          </Grid>
          <Grid item xs={12} sm={8} md={9} lg={10} sx={{position:'relative',overflow: 'auto', minHeight: '30em'}}>
            <GridGames/>
          </Grid>
      </Grid>
  )
}

interface CharacterDashboardProps {
  selectedCharacter: Character | undefined;
  setSelectedCharacter: React.Dispatch<React.SetStateAction<Character | undefined>>;
}

const CharacterDashboard = ({ selectedCharacter, setSelectedCharacter }: CharacterDashboardProps) => {
  const region: Region | undefined = selectedCharacter?.lord ? 
    Data.regions.find((r: Region) => r.name === selectedCharacter.lord.region) || undefined :
    undefined;

  const role: Role | undefined = selectedCharacter?.lord ? 
    Data.roles.find((r: Role) => r.name === selectedCharacter.lord.role) || undefined :
    undefined;

  const TypographyItem = styled(Typography)(({ theme }) => ({
    padding: theme.spacing(1),
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent:'space-between'
  }));

  return (
    <Stack spacing={2} position={'sticky'} margin={2} top={0}>
      <div style={{ padding: "1em", position: 'relative', boxShadow: '0 0 2em' }}>
        <CardLol character={selectedCharacter} useDisableRipple />
      </div>
      <Paper variant="outlined"
        sx={{
          background: `url(${region?.region_img}) center center / contain no-repeat`,
          boxShadow: 'inset 0 0 2em',
          height: '15em'
        }}>
        <Stack direction={'column'} sx={{ bgcolor: 'rgba(0,0,0,0.7)', height: '100%', justifyContent: 'space-around' }}>
          {region && (
            <TypographyItem variant="base">
              <span style={{ marginRight: '0.5em' }}>Region : {region.name}</span>
              <img src={region?.region_icon} style={{height:'3em',objectFit:'cover'}}/>
            </TypographyItem>
          )}
          {selectedCharacter?.lord && (
            <>
              <TypographyItem variant="base">Sex : {selectedCharacter.lord.sex}</TypographyItem>
              <TypographyItem variant="base">Lane : {selectedCharacter.lord.lane}</TypographyItem>
            </>
          )}
          {role && (
            <TypographyItem variant="base">
              <span style={{ marginRight: '0.5em' }}>Role : {role.name}</span>
              <img src={role?.role_icon} style={{height:'3em',objectFit:'cover'}}/>
            </TypographyItem>
          )}
        </Stack>
      </Paper>
      <PositionedMenu setSelectedCharacter={setSelectedCharacter} />
    </Stack>
  )
}


const GridGames = () => {
  return (
    <Grid
      container
      spacing={2}
      columns={{ xs: 3, sm: 3, md: 5, lg: 6, xl: 7 }}
      sx={{ position: 'absolute',padding:'10px'}}
    >
      {Data.personnages.map((personnage:Character, index:number) => (
        <Grid item key={index} xs={1} sm={1} md={1}>
          <CardLol character={personnage} useEffectShadow bottom={<InfoLordLol character={personnage}/>}/>
        </Grid>
      ))}
    </Grid>
  )
}

interface InfoLordLolProps {
  character:Character;
}

const InfoLordLol = ({character}:InfoLordLolProps) => {

  const [expanded, setExpanded] = React.useState(false);

  const toggleExpanded = (event:React.SyntheticEvent) => {
      event.stopPropagation();
      // console.log("Fonction B");
      setExpanded(!expanded);
  };

  const theme = useTheme();
  const TypographyInfoLord = styled(Typography)(({ theme }) => ({
    // padding: theme.spacing(1),
    ...theme.typography.base,
    width: '100%',
    textTransform: "none",
    fontSize:'1.1em',
  }));

  return (
      <>
          <CardActions sx={{padding:'0',width:'100%'}}>
              <Box
                sx={{
                  width:'100%',
                  position:'relative',
                }}
                >
              <Button variant="text" size="small" color="primary" onClick={toggleExpanded} style={{width:'100%',padding:0}}>
                  <Grid container columns={12} sx={{alignItems:'center',padding:'0'}}>
                      <Grid item sm={12} md={10} style={{textAlign:'left'}}>
                          <Typography 
                          variant={"subtitle"} 
                          textTransform={'lowercase'} 
                          sx={{
                            textShadow: `0px 0px 8px black`,
                            fontSize:{xs:'0.5em',lg:'1em',xl:'1.2em'},
                            lineHeight:'0.5em'
                            }}>
                              {character.description}
                          </Typography>
                      </Grid>
                      <Grid item sm={12} md={2} sx={{justifyContent:'right',fontSize:{xs:'0.8em',lg:'1em',xl:'1.2em'}}}>
                          <Icon icon="icon-park-outline:info" color={theme.palette.goldOverride.main}/>
                      </Grid>
                  </Grid>
              </Button>
              {/* Bote d'expansion */}
              {expanded && (
                  <Paper variant='outlined'
                    sx={{
                        position: 'absolute',
                        background:'#0000',
                        backdropFilter: 'blur(10px)',
                        bottom: '100%',
                        padding:'0.5em',
                        width:'100%'
                    }}
                    >
                    <Stack direction={'column'} sx={{ height: '100%', justifyContent: 'space-around' }}>
                      <TypographyInfoLord>Region : {Data.regions.find((r: Region) => r.name === character.lord.region)?.name || ''}</TypographyInfoLord>
                      <TypographyInfoLord>Sex : {character.lord.sex}</TypographyInfoLord>
                      <TypographyInfoLord>Lane : {character.lord.lane}</TypographyInfoLord>
                      <TypographyInfoLord>Role : {Data.roles.find((r: Role) => r.name === character.lord.role)?.name || ''}</TypographyInfoLord>
                    </Stack>
                  </Paper>
              )}
              </Box>
          </CardActions>
      </>
  )
}

interface PositionedMenuProps {
  setSelectedCharacter: React.Dispatch<React.SetStateAction<Character | undefined>>;
}

const PositionedMenu = ({setSelectedCharacter}:PositionedMenuProps ) => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <Button
        id="demo-positioned-button"
        aria-controls={open ? 'demo-positioned-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        variant="contained"
        onClick={handleClick}
        style={{width:"100%",fontSize:'0.8em'}}
      >
        <Typography variant="title">Select a champion</Typography>
      </Button>
      <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        style={{maxHeight:"30em"}}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        {Data.personnages.map((personnage:Character, index:number) => (
          <div key={index} >
            <MenuItem onClick={() => {handleClose(); setSelectedCharacter(personnage);}}>{personnage.name}</MenuItem>
          </div>
        ))}
      </Menu>
    </div>
  );
}