import { Grid, Stack, Typography } from "@mui/material"
import DataConfigPc from "../../../Data/DataConfigPc.json"
import CardFillImg from "../../../Components/Card/CardFillImg";

interface elementConfigProps {
    title:string;
    img:string;
    link:string;
}

const paddingMotheGrid = { xs: '0.5rem',sm:'1rem', lg: '1rem' };
const heightCard = {xs:'10rem',sm:'25rem',lg:'15rem',xl:'20rem'}

export default function ConfigPc() {
    return (
        <Stack sx={{padding:{xs:'1em',md:'2em',lg:'3em'}}}>
            <Grid container direction={{xs:'column', lg:"row"}}>
              <Grid item xs={12}>
                <Typography variant="title" component="div" gutterBottom textAlign={"center"}>
                  Config pc : ~1504
                </Typography>
              </Grid>
              <Grid item xs={6} container direction="row">
                <Grid item xs={6} container direction="column">
                  <Grid item padding={paddingMotheGrid} height={heightCard}>
                        <CardFillImg content={DataConfigPc.processeur} varianteImg={'contain'}/>
                  </Grid>
                  <Grid item padding={paddingMotheGrid} height={heightCard}>
                    <CardFillImg content={DataConfigPc.cart_mere} varianteImg={'contain'}/>
                  </Grid>
                </Grid>
                <Grid item xs={6} padding={paddingMotheGrid} >
                  <CardFillImg content={DataConfigPc.cart_graphique} varianteImg={'contain'}/>
                </Grid>
              </Grid>
              <Grid item xs={6} container direction="column">
                <Grid item xs={6} container direction="row">
                  <Grid item xs={7} padding={paddingMotheGrid} height={heightCard}>
                    <CardFillImg content={DataConfigPc.ram} varianteImg={'contain'}/>
                  </Grid>
                  <Grid item xs={5} padding={paddingMotheGrid} height={heightCard}>
                    <CardFillImg content={DataConfigPc.alimentation} varianteImg={'contain'}/>
                  </Grid>
                </Grid>
                <Grid item xs={6} container direction="row">
                    {DataConfigPc.boitier.map((element:elementConfigProps, index:number) => (
                        <Grid item key={index} xs={4} height={heightCard} padding={paddingMotheGrid}>
                            <CardFillImg content={element} varianteImg={'contain'}/>
                        </Grid>
                    ))}
                </Grid>
              </Grid>
            </Grid>
        </Stack>
    )
}