import { Navigate, RouteObject } from "react-router-dom";
import { FullScreenLayout, NormalLayout } from "../Components/Layout/Layout";
import Home from "../Pages/Home";
import Info from "../Pages/Info/Index";
import ConfigPc from "../Pages/perso/configPC";
import path from "path";

const normalRoutes: RouteObject = {
    path: "*",
    element: <NormalLayout/>,
    children: [
        {
            path:"home",
            element: <Home/>,
        },
        {
            path:"info",
            element: <Info/>,
        },
        {
            path:"*",
            element: <Navigate to={"/home"} replace />,//The replace property can be used if you'd like to replace the current entry in the history stack
        }
    ]
}

const personnalPage: RouteObject = {
    path: "p",
    element: <FullScreenLayout/>,
    children: [
        {
            path: "config",
            element: <ConfigPc/>,
        },
        {
            path:"*",
            element: <Navigate to={"/home"} replace />,
        }
    ]
}

const globalRoute: RouteObject = {
    path: "arima_kosei",
    children: [
        {
            path: "*",
            element: <NormalLayout/>,
            children: [
                {
                    path:"home",
                    element: <Home/>,
                },
                {
                    path:"info",
                    element: <Info/>,
                },
                {
                    path:"*",
                    element: <Navigate to={"/arima_kosei/home"} replace />,
                }
            ]
        },
        {
            path: "p",
            element: <FullScreenLayout/>,
            children: [
                {
                    path: "config",
                    element: <ConfigPc/>,
                },
                {
                    path:"*",
                    element: <Navigate to={"/arima_kosei/home"} replace />,
                }
            ]
        },
        {
            path:"*",
            element: <Navigate to={"/arima_kosei/home"} replace />,
        }
    ]
};

const redirectRoute: RouteObject = {
    path:"*",
    element: <Navigate to={"/arima_kosei/home"} replace />,
}

const routes: RouteObject[] = [globalRoute,redirectRoute] 

export default routes;