import { Paper,Typography,useTheme,styled } from "@mui/material"

export const InfoPage = () => {
    const theme = useTheme()
    const TitleInfo = styled(Typography)(({ theme }) => ({
        textAlign:'justify',
        ...theme.typography.title,
        color:theme.palette.goldOverride.main,
      }));
    const AnserInfo = styled(Typography)(({ theme }) => ({
        textAlign:'justify',
        ...theme.typography.subtitle,
      }));
    return (
        <Paper elevation={3} sx={{padding:'2em',margin:'10px',background:theme.palette.secondary.main}}>
            <Msg>
                <TitleInfo>Pourquoi n'y a-t-il pas d'cran "vainqueur" ?</TitleInfo>
            </Msg>
            <Msg>
                <AnserInfo>Car pour a, il y a besoin d'hberger une base de donnes centrale, et a cote de l'argent de fou !!!!</AnserInfo>
                <AnserInfo>PS : Je fais ce jeu essentiellement pour m'entraner en programmation et pour m'amuser, tranquille il doit y avoir beaucoup d'erreurs.</AnserInfo>
            </Msg>
            <Msg>
                <TitleInfo>Ceci est une bta.</TitleInfo>
            </Msg>
            <Msg>
                <AnserInfo>Voici la liste des choses qui seront ajoutes dans l'ordre de priorit :</AnserInfo>
                <AnserInfo>|</AnserInfo>
                <AnserInfo>La liste complte des champions du jeu</AnserInfo>
                <AnserInfo>Des filtres pour jouer avec un nombre rduit de champions</AnserInfo>
                <AnserInfo>Choix du champion de faon alatoire</AnserInfo>
                <AnserInfo>|</AnserInfo>
                <AnserInfo>Je ne sais pas quoi d'autre</AnserInfo>
                <AnserInfo>|</AnserInfo>
                <AnserInfo>Une base de donnes donc la possibilit de crer un compte</AnserInfo>
                <AnserInfo>La possibilit de jouer contre quelqu'un sans tre en vocal</AnserInfo>
                <AnserInfo>Un chat in-game</AnserInfo>
                <AnserInfo>Un cran de victoire</AnserInfo>
                <AnserInfo>Un tirage alatoire des champions identiques chez les deux joueurs</AnserInfo>
                <AnserInfo>( ui, une base de donnes est essentielle pour un site web, on en fait des choses avec une base de donnes)</AnserInfo>
            </Msg>

        </Paper>
    )
}

interface MsgProps {
    children?:React.ReactNode
} 
const Msg = ({children}:MsgProps) => {
    const theme = useTheme()
    return (
        <Paper variant='outlined' sx={{
            background:theme.palette.bgrdColor.main, 
            maxWidth:{xs:'100%',lg:'50%'},
            border:`1px solid ${theme.palette.goldOverride.main}50`,
            margin:'1em',
            padding:'5px'}}>
            {children}
        </Paper>
    )
}